/* eslint-disable flowtype/require-valid-file-annotation */

// GeoGlobal 框选

'use strict';

import Point from '@mapbox/point-geometry';
import DOM from '../../util/dom';
import LngLatBounds from '../../extend/geo/lng_lat_bounds';
import {bindAll} from '../../util/util';
import window from '../../util/window';

/**
 * The `BoxHandler` allows the user draw a bounding box.
 * The bounding box is defined by clicking and holding `shift` while dragging the cursor.
 *
 * @param {Map} map The Mapbox GL JS map to add the handler to.
 */
class BoxHandler {

    constructor(map) {
        this._map = map;
        this._el = map.getCanvasContainer();
        this._container = map.getContainer();

        bindAll([
            '_onMouseDown',
            '_onMouseMove',
            '_onMouseUp',
            '_onKeyDown'
        ], this);
    }

    /**
     * Returns a Boolean indicating whether the "box" interaction is enabled.
     *
     * @returns {boolean} `true` if the "box" interaction is enabled.
     */
    isEnabled() {
        return !!this._enabled;
    }

    /**
     * Returns a Boolean indicating whether the "box" interaction is active, i.e. currently being used.
     *
     * @returns {boolean} `true` if the "box" interaction is active.
     */
    isActive() {
        return !!this._active;
    }

    /**
     * Enables the "box" interaction.
     *
     * @example
     *   map.box.enable();
     */
    enable() {
        if (this.isEnabled()) return;
        this._el.addEventListener('mousedown', this._onMouseDown, false);
        this._enabled = true;

    }

    /**
     * Disables the "box" interaction.
     *
     * @example
     *   map.box.disable();
     */
    disable() {
        if (!this.isEnabled()) return;
        this._el.removeEventListener('mousedown', this._onMouseDown);
        this._enabled = false;
    }

    _onMouseDown(e) {
        if (!(e.button === 0)) return;

        window.document.addEventListener('mousemove', this._onMouseMove, false);
        window.document.addEventListener('keydown', this._onKeyDown, false);
        window.document.addEventListener('mouseup', this._onMouseUp, false);

        DOM.disableDrag();
        this._startPos = DOM.mousePos(this._el, e);
        this._active = true;
    }

    _onMouseMove(e) {
        const p0 = this._startPos,
            p1 = DOM.mousePos(this._el, e);

        if (!this._box) {
            this._box = DOM.create('div', 'mapboxgl-boxhandler', this._container);
            this._container.classList.add('mapboxgl-crosshair');
            this._fireEvent('boxstart', e);
        }

        const minX = Math.min(p0.x, p1.x),
            maxX = Math.max(p0.x, p1.x),
            minY = Math.min(p0.y, p1.y),
            maxY = Math.max(p0.y, p1.y);

        DOM.setTransform(this._box, `translate(${minX}px,${minY}px)`);

        this._box.style.width = `${maxX - minX}px`;
        this._box.style.height = `${maxY - minY}px`;
    }

    _onMouseUp(e) {
        if (e.button !== 0) return;

        const p0 = this._startPos,
            p1 = DOM.mousePos(this._el, e),
            bounds = new LngLatBounds()
                .extend(this._map.unproject(p0))
                .extend(this._map.unproject(p1));

        const box = [Point.convert(p0), Point.convert(p1)];
        const queryGeometry = [
            box[0],
            new Point(box[1].x, box[0].y),
            box[1],
            new Point(box[0].x, box[1].y)
        ];
        const boxLnglats = queryGeometry.map((p) => {
            return this._map.unproject(p);
        });

        this._finish();

        if (p0.x === p1.x && p0.y === p1.y) {
            // this._fireEvent('boxcancel', e);
            // do nothing
        } else {
            this._map.fire('boxend', {
                originalEvent: e,
                boxBounds: bounds,
                boxLnglats,
                startPos: p0,
                endPos: p1
            });
        }
    }

    _onKeyDown(e) {
        if (e.keyCode === 27) {
            this._finish();
            this._fireEvent('boxcancel', e);
        }
    }

    _finish() {
        this._active = false;

        window.document.removeEventListener('mousemove', this._onMouseMove, false);
        window.document.removeEventListener('keydown', this._onKeyDown, false);
        window.document.removeEventListener('mouseup', this._onMouseUp, false);

        this._container.classList.remove('mapboxgl-crosshair');

        if (this._box) {
            this._box.parentNode.removeChild(this._box);
            this._box = null;
        }

        DOM.enableDrag();
    }

    _fireEvent(type, e) {
        return this._map.fire(type, {originalEvent: e});
    }
}

export default BoxHandler;
